<template>
  <div class="home-container" v-if="post.length" style="overflow: auto">
    <PostListItem v-for="list in post" :key="list.id" :list="list" />
    <div v-if="loading" class="loading">加载中....</div>
  </div>
  <el-skeleton v-else :rows="18" animated />
</template>

<script>
import { mapActions, mapState } from "vuex";
import PostListItem from "../components/postListItem.vue";
import debounce from "lodash/debounce";
export default {
  components: { PostListItem },
  computed: {
    ...mapState(["postList"]),
    post() {
      return this.postList.list;
    },
    tab() {
      return this.$route.params.tab || "all";
    },
  },
  mounted() {
    window.addEventListener("scroll", this.scrollFun);
  },
  // 解除滚动条事件
  beforeDestroy() {
    window.removeEventListener("scroll", this.scrollFun);
  },
  data() {
    return {
      page: 2,
      loading: false,
    };
  },
  watch: {
    "$route.params.tab": {
      handler() {
        // 写if 判断 404页面
        
        this.getPost({ tab: this.tab });
        this.page = 2;
        // console.log(this.post);
      },
      immediate: true,
    },
  },
  // created () {
  // this.getPost()
  // console.log(this.postList);
  // },

  methods: {
    ...mapActions(["getPost", "rollPost"]),

    // 防抖函数的添加是 避免滚动条距离底部距离达到150 以下 多次触发
    // 滚动条在 300毫秒内没动 并且距离在 150 以下 才触发函数
    scrollFun: debounce(async function () {
      // console.log('滚动条事件')

      // 获取到顶部的距离 window.scrollY  document.documentElement.scrollTop
      // window 可视窗口的高度 window.innerHeight  document.documentElement.clientHeight
      // 文档的总高度 就是这个页面的总高度document.documentElement.scrollHeight

      // 获取到滚动条距离底部的距离;
      const scrollHeight = document.documentElement.scrollHeight;
      const windowHeight =
        window.innerHeight || document.documentElement.clientHeight;
      const scrollTop = window.scrollY || document.documentElement.scrollTop;
      const scrollBottom = scrollHeight - windowHeight - scrollTop;

      if (scrollBottom <= 150 && this.post.length) {
        // 因为进入页面的时候，但数据没来的时候，没有滚动条，默认可能会直接触底，但是不想触发，加了&& this.post.length
        if (!this.loading) {
          this.loading = true;
          this.page++;
          await this.getPost({ tab: this.tab, page: this.page });
          this.loading = false;
        }
      }
    }, 300),
  },
};
</script>

<style lang='less'>
.home-container {
  .loading {
    height: 100px;
    line-height: 100px;
    font-size: 20px;
    text-align: center;
  }
}
</style>